import styled from '@emotion/styled'

const Container = styled.div`
  color: "#959595";
  margin-top: 20px;
`

function TotalProfitability(props) {
  let btcPrice = Number(props?.btcPrice).toFixed(2)
  let totalProf = props?.rigData?.totalProfitability
  return (
    <Container>
      {`Mining $${(btcPrice * totalProf).toLocaleString('en-US')} / 24hrs`}
    </Container>
  );
}



export default TotalProfitability;
