import {useEffect, useState} from "react"
import './App.css';
import Balance from './Balance';
import BitcoinPrice from './BitcoinPrice';
import TotalProfitability from './TotalProfitability';
import Rigs from './Rigs';
import LoadingScreen from 'react-loading-screen';

function App() {
  const [data, setData] = useState();
  const [timeLeft, setTimeLeft] = useState(0);

  // get data from backend, fetch every 45 seconds
  // api is rate limitted, so data retrieval will occur every 45 seconds
  // this timer shows the countdown
  useEffect(() => {
    async function fetchData() {
      try {
        await fetch('/getData')
        .then(response => response.json())
        .then(data => {
          console.log(data)
          setData(data)
        });
      } catch (e) {
        console.error('Error fetching nicehash data', e);
      };
    };
    if(timeLeft===0){
      fetchData();
      setTimeLeft(45)
      console.log('Data Refreshed. Refreshing Data again in 45 seconds.');
    }
    if (!timeLeft) return;
    const intervalId = setInterval(() => {
      setTimeLeft(timeLeft - 1);
    }, 1000);
    return () => clearInterval(intervalId);
  }, [timeLeft]);

  return (
    <div className="App">
      <div onClick={()=>window.location.reload()} style={{color:"#fff", backgroundColor: "#49515F"}}>{`Refreshing data in ${timeLeft} seconds`}</div>
      <header className="App-header">
        <div style={{width: "92%"}}>
          <LoadingScreen
            loading={data === undefined}
            bgColor='#49515F'
            spinnerColor='#9ee5f8'
            textColor='#fff'
            text='Content is loading'
          > <div>
              <div>
                <TotalProfitability {...data}/>
                <BitcoinPrice {...data}/>
                <Balance {...data}/>
              </div>
              <div>
                <Rigs {...data}/>
              </div>
            </div>
          </LoadingScreen>
        </div>
        
      </header>
    </div>
  );
}

export default App;
